<template>
    <el-container>
        <el-main>
            <el-row>
                <el-col  :span="8" :offset="8">
                    <h1 class="head"></h1>
                    <el-form :model="flow" ref="flow" :rules="flowRules" label-width="80px">
                    <el-form-item label="" prop="flowName">
                        <el-input v-model="flow.flowName"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="flowDescription">
                        <el-input type="textarea" v-model="flow.flowDescription"></el-input>
                    </el-form-item>
                    <el-form-item class="addFlowBtn">
                        <el-button type="success" @click="addFLow('flow')"></el-button>
                    </el-form-item>
                    </el-form>
                </el-col>
            </el-row>
        </el-main>
    </el-container>
</template>

<script>
export default {
    computed: {
        
    },
    data() {
        return {
            flow: {
                flowName: '',
                flowDescription: ''
            },
            flowRules:{
                flowName:[
                    {required: true, message: '', trigger: 'blur'}
                ],
                flowDescription:[
                    {required: true, message: '', trigger: 'blur'}
                ]
            }
        }
    },
    methods: {
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        addFLow(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    //vuex
                    this.$store.commit('updateFlowName',this.flow.flowName);
                    this.$store.commit('UpdateFlowDescription',this.flow.flowDescription);
                    this.$message({
                        type:'success',
                        message:''
                    })
                    //
                    this.$store.commit('updateCurrentSteps',2)
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        }
    },
}
</script>

<style scoped>
    .head{
        text-align: center;
    }
    .addFlowBtn{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }
</style>