<template>
  <el-container>
        <el-main>
            <el-row>
                <el-col :span="10" :offset="7">
                    <el-card class="box-card">
                 <div slot="header" class="clearfix">
                    <span></span>
                    <el-button style="float: right; padding: 10px 10px" type="success" @click="commit"></el-button>
                    <el-button style="margin-right:5px; float: right; padding: 10px 10px" @click="prev" type="info"></el-button>
                </div>
                <div  class="text item">
                   :{{flow.flowName}}
                </div>
                 <div  class="text item">
                   :{{flow.flowDescription}}
                </div>
                <div  class="text item">
                   :
                </div>
                <div v-for="(item,index) in flowNodeList" :key='index' class="text item">
                    {{item.flowNodeName}}&nbsp&nbsp&nbsp {{item.flowNodeDescription}}&nbsp&nbsp&nbsp {{showPositionName(item.position.positionId)}}
                </div>
            </el-card>
                </el-col>
            </el-row>
            
        </el-main>
    </el-container>
</template>

<script>
export default {
    data() {
        return {
            flow:this.$store.state.flow,
            flowNodeList:this.$store.state.flowNodeList,
            positionList:this.$store.state.positionList
        }
    },
    methods: {    
        //
        commit(){
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'success'
            }).then(() => {
               //
               this.axios.post('/flow',{
                   flowName:this.flow.flowName,
                   flowDescription:this.flow.flowName,
                   flowNodeList:this.flowNodeList
               })
               .then((res)=>{
                  if(res.data.status == 0){
                      this.$message({
                        type: 'success',
                        message: res.data.msg
                    });  
                    this.$store.commit('updateCurrentSteps',1)
                  }
                  else{
                    this.$message({
                        type: 'error',
                        message: res.data.msg
                    });  
                  }
               })
               .catch((res)=>{
                   this.$message({
                        type: 'warning',
                        message: ''
                    });      
               })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });          
            });
        },
        prev(){
            this.$store.commit('updateCurrentSteps',3)
        },
        //
        showPositionName(positionId){
            for(var i = 0;i<this.positionList.length;i++){
                if(this.positionList[i].positionId == positionId){
                    return this.positionList[i].positionName
                }
            }
        },
    },
}
</script>

<style scoped>
  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 100%;
  }
</style>