<template>
  <el-table
    :data="articleList"
    style="width: 100%"
    :default-sort = "{prop: 'articleCreateTime', order: 'descending'}">
    <el-table-column
      label=""
      type="index">
    </el-table-column>
    <el-table-column
      label=""
      sortable
      prop="articleCreateTime">
    <template slot-scope="scope">
        {{scope.row.articleCreateTime | dateFormart}}
    </template>
    </el-table-column>
    <el-table-column
      label=""
      prop="articleTitle">
    </el-table-column>
    <el-table-column
      label=""
      sortable
      prop="articleCommentCount">
    </el-table-column>
    <el-table-column
      label=""
      sortable
      prop="articlePageView">
    </el-table-column>
    <el-table-column
      label=""
      prop="articleType">
    <template slot-scope="scope">
        {{scope.row.articleType == 0?'':''}}
    </template>
    </el-table-column>
    <el-table-column
      label=""
      prop="articleState"
      :filters="[{ text: '', value: 0 }, { text: '', value: 1 }]"
      :filter-method="filterState"
      filter-placement="bottom-end">
      <template slot-scope="scope">
        <el-tag
          :type="scope.row.articleState == 0 ? 'success' : 'danger'"
          disable-transitions>{{scope.row.articleState == 0?"":''}}</el-tag>
      </template>
    </el-table-column>
    <el-table-column
      align="right"
      width="300">
      <template slot="header" slot-scope="scope">
        <el-input
          v-model="search"
          size="mini"
          placeholder=""/>
      </template>
      <template slot-scope="scope">
           <el-button
          size="mini"
          type="primary"
          plain
          @click="articleInfo(scope.$index, scope.row)"></el-button>
        <el-button
          size="mini"
          type="danger"
          plain
          @click="delArticle(scope.$index, scope.row)"></el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
export default {
    computed: {
    
    },
    data() {
        return {
            articleList:[],
            search: ''
        }
    },
    methods: {
      filterState(value, row){
        return row.articleState == value;
      },
      articleInfo(index, row) {
          sessionStorage.setItem("articleDetailsId",row.articleId);
          console.log(index, row);
          this.$router.push({path:'/userHome/articleInfo'})
      },
      //
      delArticle(index, row) {
         this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'error'
        }).then(() => {
          this.axios.delete("/article/"+row.articleId)
          .then((res)=>{
            if(res.data.status == 0){
              this.$message({
                type: 'success',
                message: res.data.msg
             }); 
             this.getArticleList()
            }
            else{
              this.$message({
                type: 'error',
                message: res.data.msg
             }); 
            }

          })
          .catch((res)=>{
            this.$message({
              type: 'warning',
              message: ''
            });  
          })

        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
      },
      getArticleList(){
          var info= JSON.parse(sessionStorage.getItem('userInfo'));
          this.axios.get("/article/userArticle/"+info.userId)
          .then((res)=>{
              if(res.data.status == 0){
                  this.articleList = res.data.data.articleList
              }
              else{
                  this.$message({
                  type: 'error',
                  message: res.data.msg
              }); 
              }
          })
          .catch((res)=>{
              this.$message({
                  type: 'warning',
                  message: ""
              }); 
          })
      }
    },
    created() {
        this.getArticleList()
    },
}
</script>

