<template>
  <el-container>
      <el-main>
        <el-table
          :data="showList"
          style="width: 100%">
          <el-table-column
            label=""
            type="index">
          </el-table-column>
          <el-table-column
            label=""
            prop="applyTitle">
          </el-table-column>
          <el-table-column
            label=""
            prop="applyDescription">
          </el-table-column>
          <el-table-column
            label=""
            prop="flow.flowName">
          </el-table-column>
          <el-table-column
            label="">
            <template slot-scope="scope">
            <el-tag ></el-tag>
          </template>
          </el-table-column>
          <el-table-column
            align="right">
            <template slot="header" slot-scope="scope">
              <el-input
                v-model="search"
                size="mini"
                placeholder=""/>
            </template>
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="primary"
                plain
                @click="detailsApply(scope.$index, scope.row)"></el-button>
                <el-button
                size="mini"
                type="success"
                plain
                @click="examine(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-dialog
          title=""
          :visible.sync="dialogVisible"
          width="50%">
          <el-card v-if="applyInfo!=null" shadow="always">
            <div slot="header" class="clearfix">
              <span></span>
            </div>
            <div  class="text item">
              {{applyInfo.applyTitle}}
            </div>
            <div  class="text item">
              {{applyInfo.applyDescription}}
            </div>
            <div  class="text item">
              <el-tag ></el-tag>
            </div>
            <div  class="text item">
              {{applyInfo.flow.flowName}}
            </div>
             <div  class="text item">
              
              <ul style="overflow:auto;list-style:none">
                <li v-for="item in applyInfo.fileList" :key="item.fileId">
                    <span class="file-item">{{item.fileName}} </span>
                    <span class="file-item">{{Math.round( item.fileSize/1024)}} K</span>
                    <el-button type="text" @click="downloadApplyFile(item.fileId,item.fileName)"></el-button>
                </li>
              </ul>
            </div>
            <div  class="text item">
              
              <el-table :data="applyInfo.examineList">
                <el-table-column  type="index" label=""></el-table-column>
                <el-table-column property="userName" label=""></el-table-column>
                <el-table-column property="examineInfo" label="" ></el-table-column>
                <el-table-column  label="">
                  <template slot-scope="scope">
                    {{scope.row.examineDate |dateFormart}}
                  </template>
                </el-table-column>
                <el-table-column label="" width="100">
                  <template slot-scope="scope">
                    <el-tag :type="scope.row.examineStatus?'success':'danger'" >
                      {{scope.row.examineStatus?'':''}}
                    </el-tag>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </el-card>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click="dialogVisible = false"> </el-button>
          </span>
        </el-dialog>

        <el-dialog title="" :visible.sync="dialogFormVisible">
          <el-form label-position="left" :model="examineForm">
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="examineForm.userName" placeholder="" ></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-radio v-model="examineForm.examineStatus" label=true border></el-radio>
              <el-radio v-model="examineForm.examineStatus" label=false border></el-radio>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input 
                type="textarea" 
                :autosize="{ minRows: 4, maxRows: 4}" 
                v-model="examineForm.examineInfo"
                placeholder="">
                </el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button type="primary" @click="commitExamine"> </el-button>
          </div>
        </el-dialog>
      </el-main>
  </el-container>
</template>

<script>
export default {
  computed: {
    showList:function(){
      return this.applyList.filter((item)=>{
        if(item.applyTitle.includes(this.search)||item.flow.flowName.includes(this.search)){
          return item
        }
      })
    }
  },
  data() {
    return {
      dialogFormVisible:false,
      dialogVisible: false,
      applyList: [],
      search: '',
      applyInfo:null,
      formLabelWidth:'120px',
      examineForm:{
        userName:'',
        examineInfo:'',
        examineStatus:'true',
        apply:{
          applyId:''
        },
        flowNode:null
      }
    }
  },
  methods: {
    //
    commitExamine(){
      this.axios.post('/examine',this.examineForm)
      .then((res)=>{
        if(res.data.status == 0){
          this.getList()
          this.$message.success(res.data.msg)
          this.dialogFormVisible = true
        }
        else{
          this.$message.error(res.data.msg)
        }
      })
    },
    //
    examine(index, row){
      this.examineForm.apply.applyId = row.applyId
      this.flowNode = row.applyCurrentNode
      this.dialogFormVisible = true
    },
    //
    downloadApplyFile(fileId,fileName){
      this.axios.get("/applyFile/download/"+fileId,{
        responseType:'blob'
      })
      .then((res)=>{
         console.log(res)
        if (window.navigator.msSaveBlob) {
          try {
          const blobObject = new Blob([res.data]);
          window.navigator.msSaveBlob(blobObject, fileName);
          } catch (e) {
            console.log(e);
          }
      } else {
          const blob = res.data;
          const reader = new FileReader();
          reader.readAsDataURL(blob);
          reader.onload = (e) => {
          const a = document.createElement('a');
          a.download = fileName;
          a.href = e.target.result;
          document.body.appendChild(a);
          a.click();
          document.body.removeChild(a);
          }
      }
      })
      .catch((res)=>{

      })
    },
    //
    detailsApply(index, row) {
      this.axios.get("/apply/"+row.applyId)
      .then((res)=>{
        console.log(res)
        if(res.data.status == 0){
          this.applyInfo = res.data.data.apply
          this.dialogVisible = true
        }
        else{
          this.$message.error(res.data.msg)
        }
      })
      .catch((res)=>{

      })
    },
    //
    getList(){
      var info = JSON.parse(sessionStorage.getItem('userInfo'));
      this.axios.get('/examineApplyList/'+info.userId)
      .then((res)=>{
        console.log(res)
        this.applyList = res.data.data.examineApplyList
      })
    }
  },
  created() {
    this.getList()
  },
}
</script>

<style>

</style>