<template>
    <el-container direction="vertical">
      <el-header>
        <el-button  type="success" @click="addDepFormVisible = true"></el-button>
      </el-header>
      <el-main style="padding:0px">
        <!--  -->
        <el-dialog title="" :visible.sync="addDepFormVisible" width="30%" center>
          <div class="add-dep-from">
          <el-form :label-position="'top'" :model="addDepForm" ref="addDepForm" :rules="addDepFormRules"> 
             <el-form-item label="" prop="departmentLevel">
               <el-select v-model="addDepForm.departmentLevel" placeholder="">
                 <el-option key="1" :value=1 :label="''"></el-option>
                 <el-option key="2" :value=2 :label="''"></el-option>
                 <el-option key="3" :value=3 :label="''"></el-option>
                 <el-option key="4" :value=4 :label="''"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item prop="departmentParent.departmentId" v-if="addDepForm.departmentLevel!=1" label="">
                <el-select v-model="addDepForm.departmentParent.departmentId" clearable placeholder="">
                  <el-option
                    v-for="item in parentDepList"
                    :key="item.departmentId"
                    :label="item.departmentParent==null?item.departmentName:item.departmentParent.departmentName+'/'+item.departmentName"
                    :value="item.departmentId">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="" prop="departmentName">
                <el-input v-model="addDepForm.departmentName" placeholder=""></el-input>
              </el-form-item>
          </el-form>
          </div>
          <div slot="footer" class="dialog-footer">
            <el-button @click="clearAddDepForm"> </el-button>
            <el-button type="primary" @click="addDepartement('addDepForm')"> </el-button>
          </div>
        </el-dialog>
          <!--  -->
        <el-table
          :data="showDepartmentList"
          style="width: 100%"
          height="540">
          <el-table-column
          type="index"
          label="">
          </el-table-column>
          <el-table-column
          prop="departmentName"
          label="">
          </el-table-column>
          <el-table-column
          prop="departmentNumber"
          label="">
          </el-table-column>
          <el-table-column
          label="">
          <template slot-scope="scope">
            {{scope.row.departmentParent==null?"":scope.row.departmentParent.departmentName}}
          </template>
          </el-table-column>
          <el-table-column
          prop="departmentLevel"
          label="">
          </el-table-column>
          <el-table-column
          align="right">
          <template slot="header" slot-scope="scope">
            <el-input
              v-model="search"
              size="mini"
              placeholder=""/>
          </template>
          <template slot-scope="scope">
            <el-button size="mini" type="primary" plain></el-button>
            <el-button size="mini" type="warning"@click="updateDep(scope.$index, scope.row)" plain></el-button>
            <el-button size="mini" type="danger" @click="deleteDep(scope.$index, scope.row)" plain></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
    </el-container>
</template>

<script>
  export default {
    watch:{
      'addDepForm.departmentLevel':function(){
          //
          this.addDepForm.departmentParent.departmentId = null
      }
    },
    computed:{
      parentDepList:function(){
        return this.departmentList.filter((item)=>{
          if(item.departmentLevel == this.addDepForm.departmentLevel-1){
            return item
          }
        })
      },
      showDepartmentList:function(){
        return this.departmentList.filter((item)=>{
          if(item.departmentName.includes(this.search)||
          (item.departmentParent!=null && item.departmentParent.departmentName.includes(this.search))){
            return item
          }
          
        })
      }
    },
    data() {
      return {
        addDepForm:{
          departmentLevel:1,
          departmentParent:{
            departmentId:null
          },
          departmentName:''
        },
        addDepFormRules:{
          departmentLevel:[
            {required: true, message: '', trigger: 'blur'}
          ],
          departmentName:[
             { required: true, message: '', trigger: 'blur' }
          ],
          departmentParent:{
            departmentId:[
              { required: true, message: '', trigger: 'blur' }
            ]
          }
        },
        addDepFormVisible:false,
        search: '',
        departmentList:[]
      }
    },
    methods: {
      //
      updateDep(index,row){
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
        }).then(({ value }) => {
          this.axios.put("/department/"+row.departmentId,{departmentName:value})
          .then((res)=>{
            if(res.data.status == 0){
              this.$message({
                type: 'success',
                message: res.data.msg
              });  
              this.getDepartmentList()  
            }
            else{
              this.$message({
                type: 'error',
                message: res.data.msg
              }); 
            }
          })
          .catch((res)=>{
            this.$message({
              type: 'warning',
              message: ''
            });    
          })



        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });       
        });
      },
      //
      deleteDep(index,row){
        //
        this.$confirm('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            //
            this.axios.delete("/department/"+row.departmentId)
            .then((res)=>{
                //
                if(res.data.status == 0){
                  this.$message({
                    type: 'success',
                    message: res.data.msg
                  }); 
                  //
                  this.getDepartmentList()
                }
                else{
                  this.$message({
                    type: 'error',
                    message: res.data.msg
                  }); 
                }
            })
            .catch((res)=>{
              this.$message({
                type: 'warning',
                message: ''
              });  
            })
            
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });          
          });
      },
      //
      clearAddDepForm(){
        this.addDepForm.departmentLevel = 1
        this.addDepForm.departmentParent.departmentId = null
        this.addDepForm.departmentName = ''
        this.addDepFormVisible = false
      },
      //
      addDepartement(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.axios.post("/department/add",this.addDepForm)
            .then((res)=>{
              console.log(res)
              if(res.data.status == 0){
                this.departmentList.push(res.data.data.newDepartment)
                this.$message({
                  type: 'success',
                  message: res.data.msg
                }); 
              }
              else{
                this.$message({
                  type: 'error',
                  message: res.data.msg
                }); 
              }

              this.clearAddDepForm()
            })
            .catch((res)=>{
              this.$message({
                  type: 'error',
                  message: ""
                }); 
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
        
      },
      //
      getDepartmentList(){
        this.axios.get("/department/departmentList")
        .then((res)=>{
          console.log(res)
          if(res.data.status == 0){
            this.departmentList = res.data.data.departmentList
            this.$message({
              type: 'success',
              message: res.data.msg
            }); 
          }
          else{
            this.$message({
              type: 'warning',
              message: res.data.msg
            }); 
          }
        })
        .catch((res)=>{
           this.$message({
            type: 'warning',
            message: ""
          }); 
        })
      }
    },
    created() {
      this.getDepartmentList()
    },
  }
</script>

<style scoped>
.add-dep-from{
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
  .right{
    position: absolute;
    right: 20px;
  }
</style>