<template>
    <el-container direction="vertical">
      <el-header>
        <!--  -->
        <el-form :inline="true" class="demo-form-inline">
            <el-form-item label="">
                <el-date-picker
                    v-model="createDate"
                    type="daterange"
                    range-separator=""
                    start-placeholder=""
                    end-placeholder="">
                </el-date-picker>
            </el-form-item>
            <el-form-item class="right">
                <el-button type="success" @click="createUser" ></el-button>
            </el-form-item>
        </el-form>
      </el-header>
      <el-main style="padding:0px">
          <!--  -->
        <el-table
            :data="showUserList"
            style="width: 100%"
            :default-sort = "{prop: 'userCreateTime', order: 'descending'}"
            height="540">
            <el-table-column
            type="index"
            label="">
            </el-table-column>
            <el-table-column
            prop="userCreateTime"
            sortable
            label="">
            <template slot-scope="scope">
              {{scope.row.userCreateTime | dateFormart}}
            </template>
            </el-table-column>
            <el-table-column
            prop="userAccount"
            label="">
            </el-table-column>
            <el-table-column
            prop="userName"
            label="">
            </el-table-column>
            <el-table-column
            prop="department.departmentName"
            label="">
            </el-table-column>
            <el-table-column
            prop="position.positionName"
            label="">
            </el-table-column>
            <el-table-column
            prop="userPhone"
            label="">
            </el-table-column>
            <el-table-column
            prop="userStatus"
            label=""
            :filters="[{ text: '', value: true }, { text: '', value: false }]"
            :filter-method="filterStatus"
            filter-placement="bottom-end">
            <template slot-scope="scope">
                <el-tag
                :type="scope.row.userStatus == true ? 'success' : 'warning'"
                disable-transitions>{{scope.row.userStatus == true ? '':''}}</el-tag>
            </template>
            </el-table-column>
            <el-table-column
            align="right"
            width="200">
            <template slot="header" slot-scope="scope">
              <el-input
                v-model="search"
                size="mini"
                placeholder=""/>
            </template>
            <template slot-scope="scope">
            <el-dropdown>
              <el-button size="mini" type="primary">
                <i class="el-icon-arrow-down el-icon--right"></i>
              </el-button>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item>
                  <el-button type="text" icon="iconfont icon-myIconxiangqing" @click="userDetails(scope.$index, scope.row)"></el-button>
                </el-dropdown-item>
                <el-dropdown-item>
                  <el-button type="text" icon="iconfont icon-myIconbumen" @click="setDep(scope.$index, scope.row)"></el-button>
                </el-dropdown-item>
                <el-dropdown-item>
                  <el-button type="text" icon="iconfont icon-myIconzhiwei" @click="setPos(scope.$index, scope.row)"></el-button>
                </el-dropdown-item>
                <el-dropdown-item >
                  <el-button type="text" icon="iconfont icon-myIconiconset0192" @click="userUnlock(scope.$index, scope.row)"></el-button>
                </el-dropdown-item>
                <el-dropdown-item >
                  <el-button type="text" icon="iconfont icon-myIconsuoding" @click="userLock(scope.$index, scope.row)"></el-button>
                </el-dropdown-item>
                <el-dropdown-item >
                  <el-button type="text" icon="iconfont icon-myIconzhongzhi" @click="userReset(scope.$index, scope.row)"></el-button>
                </el-dropdown-item>
                <el-dropdown-item >
                  <el-button type="text" icon="iconfont icon-myIconzhuxiao" @click="userCancel(scope.$index, scope.row)"></el-button>
                </el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
            </template>
          </el-table-column>
        </el-table>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="30%">
      <p>{{newAccount}}</p>
      <p>123456</p>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="createAccount"></el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setDepDialogVisible"
      width="25%">
        <el-form ref="setDepFrom" :model="setDepFrom" label-width="80px">
          <el-form-item label="">
          <el-select v-model="setDepFrom.department.departmentId" filterable placeholder="">
          <el-option
            v-for="item in departmentList"
            :key="item.departmentId"
            :label="item.departmentParent==null?item.departmentName:item.departmentParent.departmentName+'/'+item.departmentName"
            :value="item.departmentId">
          </el-option>
        </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="clearSetDepForm"> </el-button>
        <el-button type="primary" @click="commitSetDep('setDepFrom')"> </el-button>
      </span>
    </el-dialog>

    <el-dialog
      title=""
      :visible.sync="setPosDialogVisible"
      width="25%">
        <el-form ref="setPosFrom" :model="setPosFrom" label-width="80px">
          <el-form-item label="">
          <el-select v-model="setPosFrom.position.positionId" filterable placeholder="">
          <el-option
            v-for="item in positionList"
            :key="item.positionId"
            :label="item.positionName"
            :value="item.positionId">
          </el-option>
        </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="clearSetPosForm"> </el-button>
        <el-button type="primary" @click="commitSetPos('setPosFrom')"> </el-button>
      </span>
    </el-dialog>
    </el-main>
    </el-container>
</template>

<script>
  export default {
    computed:{
      showUserList:function(){
        return this.userList.filter((item)=>{
          if(this.createDate != null){
            if((item.userCreateTime >= this.createDate[0].getTime() && item.userCreateTime <= this.createDate[1].getTime() )&&(item.userAccount.includes(this.search)||(item.userName!=null&&item.userName.includes(this.search))||
            (item.userPhone!=null&&item.userPhone.includes(this.search))||(item.userEmail!=null&&item.userEmail.includes(this.search)))){
              return item;
            }
          }
          else{
            //
            if(item.userAccount.includes(this.search)||(item.userName!=null&&item.userName.includes(this.search))||
            (item.userPhone!=null&&item.userPhone.includes(this.search))||(item.userEmail!=null&&item.userEmail.includes(this.search))){
              return item;
            }
          }
          
        })
      }
    },
    data() {
      return {
        setDepFrom:{
          userId:'',
          department:{
            departmentId:''
          }
        },
        setPosFrom:{
          userId:'',
          position:{
            positionId:''
          }
        },
        setPosDialogVisible:false,
        setDepDialogVisible:false,
        newAccount:'',
        createDate:null,
        search: '',
        dialogVisible:false,
        userList:[],
        departmentList:[],
        positionList:[]
      }
    },
    methods: {
      clearSetPosForm(){
        this.setPosFrom.userId = ''
        this.setPosFrom.position.positionId = ''
        this.setPosDialogVisible = false
      },
      commitSetPos(formName){
        this.axios.put("/user/"+this.setPosFrom.userId,this.setPosFrom)
        .then((res)=>{
          if(res.data.status == 0){
             this.$message({
              type:'success',
              message:res.data.msg
            })
            this.getUserList()
          }
          else{
             this.$message({
              type:'error',
              message:res.data.msg
            })
          }
          this.clearSetPosForm()
        })
        .catch((res)=>{
             this.$message({
              type:'warning',
              message:""
            })
        })
      },
      clearSetDepForm(){
        this.setDepFrom.userId = ''
        this.setDepFrom.department.departmentId = ''
        this.setDepDialogVisible = false
      },
      commitSetDep(formName){
        this.axios.put("/user/"+this.setDepFrom.userId,this.setDepFrom)
        .then((res)=>{
          if(res.data.status == 0){
             this.$message({
              type:'success',
              message:res.data.msg
            })
            this.getUserList()
          }
          else{
             this.$message({
              type:'error',
              message:res.data.msg
            })
          }
          this.clearSetDepForm()
        })
        .catch((res)=>{
             this.$message({
              type:'warning',
              message:""
            })
        })
      },
      //
      setPos(index,row){
        this.setPosFrom.userId = row.userId;
        this.setPosDialogVisible = true
      },
      //
      setDep(index,row){
        this.setDepFrom.userId = row.userId;
        this.setDepDialogVisible = true;
      },
      //
      userDetails(index,row){
        console.log(row)
      },
      //
      userUnlock(index,row){
        //
        if(row.userStatus){
          this.$message({
            type:'warning',
            message:""
          })
          return;
        }else{
          //
          this.$confirm('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'success'
          }).then(() => {
            //
            this.axios.put("/user/"+row.userId,{userStatus:true})
            .then((res)=>{
                //
                if(res.data.status == 0){
                  this.$message({
                    type: 'success',
                    message: res.data.msg
                  }); 
                  //
                  this.getUserList()
                }
                else{
                  this.$message({
                    type: 'error',
                    message: res.data.msg
                  }); 
                }
            })
            .catch((res)=>{
              this.$message({
                type: 'warning',
                message: ''
              });  
            })
            
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });          
          });
        }
      },
      //
      userLock(index,row){
        console.log(row)
        //
        if(!row.userStatus){
          this.$message({
            type:'warning',
            message:""
          })
          return;
        }else{
          //
          this.$confirm('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            //
            this.axios.put("/user/"+row.userId,{userStatus:false})
            .then((res)=>{
                //
                if(res.data.status == 0){
                  this.$message({
                    type: 'success',
                    message: res.data.msg
                  }); 
                  //
                  this.getUserList()
                }
                else{
                  this.$message({
                    type: 'error',
                    message: res.data.msg
                  }); 
                }
            })
            .catch((res)=>{
              this.$message({
                type: 'warning',
                message: ''
              });  
            })
            
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });          
          });
        }
      },
      //
      userReset(index,row){
          //
          this.$confirm('123456', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            //
            this.axios.put("/user/"+row.userId,{userAccount:row.userAccount,userPassword:"123456"})
            .then((res)=>{
                //
                if(res.data.status == 0){
                  this.$message({
                    type: 'success',
                    message: res.data.msg
                  }); 
                  //
                  this.getUserList()
                }
                else{
                  this.$message({
                    type: 'error',
                    message: res.data.msg
                  }); 
                }
            })
            .catch((res)=>{
              this.$message({
                type: 'warning',
                message: ''
              });  
            })
            
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });          
          });
      },
      //
      userCancel(index,row){
        //
          this.$confirm('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            //
            this.axios.delete("/user/"+row.userId)
            .then((res)=>{
                //
                if(res.data.status == 0){
                  this.$message({
                    type: 'success',
                    message: res.data.msg
                  }); 
                  //
                  this.getUserList()
                }
                else{
                  this.$message({
                    type: 'error',
                    message: res.data.msg
                  }); 
                }
            })
            .catch((res)=>{
              this.$message({
                type: 'warning',
                message: ''
              });  
            })
            
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });          
          });
      },
      //
      createAccount(){
        this.axios.post('user/add',{})
        .then((res)=>{
          console.log(res)
          if(res.data.status == 0){
            //
            this.newAccount = res.data.data.newUser.userAccount
            this.userList.push(res.data.data.newUser)
            this.$message({
              type:'success',
              message:res.data.msg
            })
          }
          else{
            this.$message({
              type:'error',
              message:res.data.msg
            })
          }
        })
        .catch((res)=>{
            this.$message({
              type:'warning',
              message:""
            })
        })

      },
      //
      filterStatus(value, row) {
        return row.userStatus == value;
      },
      //
      createUser(){
          this.dialogVisible = true
      },
      getUserList(){
        this.axios.get("/user/userList")
        .then((res)=>{
          console.log(res)
          if(res.data.status == 0){
            this.userList = res.data.data.userList
            this.$message({
              message: res.data.msg,
              type: 'success'
            })
          }
          else{
            this.$message({
              message: res.data.msg,
              type: 'error'
            })
          }

        })
        .catch((res)=>{
          this.$message({
              message: "",
              type: 'warning'
            })
        })
      },
      //
      getDepartmentList(){
        this.axios.get("/department/departmentList")
        .then((res)=>{
          console.log(res)
          if(res.data.status == 0){
            this.departmentList = res.data.data.departmentList
            this.$message({
              type: 'success',
              message: res.data.msg
            }); 
          }
          else{
            this.$message({
              type: 'warning',
              message: res.data.msg
            }); 
          }
        })
        .catch((res)=>{
           this.$message({
            type: 'warning',
            message: ""
          }); 
        })
      },
      getPositionList(){
          this.axios.get('/position/positionList')
          .then((res)=>{
              console.log(res)
              if(res.data.status == 0){
                  this.$message({
                      type: 'success',
                      message: res.data.msg
                  }); 
                  this.positionList = res.data.data.positionList
              }
              else{
                  this.$message({
                      type: 'error',
                      message: res.data.msg
                  }); 
              }
          })
          .catch((res)=>{
              this.$message({
                  type: 'warning',
                  message: ""
              }); 
          })
        },
    },
    created() {
      this.getUserList()
      this.getDepartmentList()
      this.getPositionList()
    },
  }
</script>

<style scoped>
  .right{
    position: absolute;
    right: 20px;
  }
</style>