<template>
  <el-container>
      <el-main>
        <el-table
          :data="examineList"
          style="width: 100%">
          <el-table-column
            label=""
            type="index">
          </el-table-column>
          <el-table-column
            label=""
            prop="apply.flow.flowName">
          </el-table-column>
          <el-table-column
            label=""
            prop="flowNode.flowNodeName">
          </el-table-column>
          <el-table-column
            label="">
            <template slot-scope="scope">
                {{scope.row.userName==''?scope.row.user.userName:scope.row.userName}}
            </template>
          </el-table-column>
          <el-table-column
            label=""
            prop="examineInfo">
          </el-table-column>
          <el-table-column
            label="">
            <template slot-scope="scope">
              <el-tag :type="scope.row.examineStatus?'success':'danger'" >
                {{scope.row.examineStatus?'':''}}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column
            label="">
            <template slot-scope="scope">
              {{scope.row.examineDate|dateFormart}}
            </template>
          </el-table-column>
          <el-table-column
            align="right">
            <template slot="header" slot-scope="scope">
              <el-input
                v-model="search"
                size="mini"
                placeholder=""/>
            </template>
            <template slot-scope="scope">
               <el-button
                size="mini"
                type="primary"
                plain
                @click="detailsApply(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-dialog
          title=""
          :visible.sync="dialogVisible"
          width="50%">
          <el-card v-if="applyInfo!=null" shadow="always">
            <div slot="header" class="clearfix">
              <span></span>
            </div>
            <div  class="text item">
              {{applyInfo.applyTitle}}
            </div>
            <div  class="text item">
              {{applyInfo.applyDescription}}
            </div>
            <div  class="text item">
              <el-tag ></el-tag>
            </div>
            <div  class="text item">
              {{applyInfo.flow.flowName}}
            </div>
             <div  class="text item">
              
              <ul style="overflow:auto;list-style:none">
                <li v-for="item in applyInfo.fileList" :key="item.fileId">
                    <span class="file-item">{{item.fileName}} </span>
                    <span class="file-item">{{Math.round( item.fileSize/1024)}} K</span>
                    <el-button type="text" @click="downloadApplyFile(item.fileId,item.fileName)"></el-button>
                </li>
              </ul>
            </div>
            <div  class="text item">
              
              <el-table :data="applyInfo.examineList">
                <el-table-column  type="index" label=""></el-table-column>
                <el-table-column property="userName" label=""></el-table-column>
                <el-table-column property="examineInfo" label="" ></el-table-column>
                <el-table-column  label="">
                  <template slot-scope="scope">
                    {{scope.row.examineDate |dateFormart}}
                  </template>
                </el-table-column>
                <el-table-column label="" width="100">
                  <template slot-scope="scope">
                    <el-tag :type="scope.row.examineStatus?'success':'danger'" >
                      {{scope.row.examineStatus?'':''}}
                    </el-tag>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </el-card>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click="dialogVisible = false"> </el-button>
          </span>
        </el-dialog>
      </el-main>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      dialogVisible: false,
      examineList:[],
      applyInfo:null,
      search:''
    }
  },
  methods: {
     //
    detailsApply(index, row) {
      this.axios.get("/apply/"+row.apply.applyId)
      .then((res)=>{
        console.log(res)
        if(res.data.status == 0){
          this.applyInfo = res.data.data.apply
          this.dialogVisible = true
        }
        else{
          this.$message.error(res.data.msg)
        }
      })
      .catch((res)=>{

      })
    },
    getlist(){
      var info = JSON.parse(sessionStorage.getItem('userInfo'));
      this.axios.get('/examineList/'+info.userId)
      .then((res)=>{
        console.log(res)
        if(res.data.status == 0){
          this.examineList = res.data.data.examineList;
        }
        else{
          this.$message.error(res.data.msg)
        }
      })
      .catch((res)=>{
        console.log(res)
      })
    }
  },
  created() {
    this.getlist()
  },
}
</script>

<style>

</style>