<template>
  <el-container>
      <el-main>
        <el-table
          :data="showList"
          style="width: 100%">
          <el-table-column
            label=""
            type="index">
          </el-table-column>
          <el-table-column
            label=""
            prop="applyTitle">
          </el-table-column>
          <el-table-column
            label=""
            prop="applyDescription">
          </el-table-column>
          <el-table-column
            label=""
            prop="flow.flowName">
          </el-table-column>
          <el-table-column
            label="">
            <template slot-scope="scope">
            <el-tag ></el-tag>
          </template>
          </el-table-column>
          <el-table-column
            align="right">
            <template slot="header" slot-scope="scope">
              <el-input
                v-model="search"
                size="mini"
                placeholder=""/>
            </template>
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="primary"
                plain
                @click="detailsApply(scope.$index, scope.row)"></el-button>
                <el-button
                size="mini"
                type="warning"
                plain
                @click="cancelApply(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-dialog
          title=""
          :visible.sync="dialogVisible"
          width="50%">
          <el-card v-if="applyInfo!=null" shadow="always">
            <div slot="header" class="clearfix">
              <span></span>
            </div>
            <div  class="text item">
              {{applyInfo.applyTitle}}
            </div>
            <div  class="text item">
              {{applyInfo.applyDescription}}
            </div>
            <div  class="text item">
              <el-tag ></el-tag>
            </div>
            <div  class="text item">
              {{applyInfo.flow.flowName}}
            </div>
             <div  class="text item">
              
              <ul style="overflow:auto;list-style:none">
                <li v-for="item in applyInfo.fileList" :key="item.fileId">
                    <span class="file-item">{{item.fileName}} </span>
                    <span class="file-item">{{Math.round( item.fileSize/1024)}} K</span>
                    <el-button type="text" @click="downloadApplyFile(item.fileId,item.fileName)"></el-button>
                </li>
              </ul>
            </div>
            <div  class="text item">
              
              <el-table :data="applyInfo.examineList">
                <el-table-column  type="index" label=""></el-table-column>
                <el-table-column property="userName" label=""></el-table-column>
                <el-table-column property="examineInfo" label="" ></el-table-column>
                <el-table-column  label="">
                  <template slot-scope="scope">
                    {{scope.row.examineDate |dateFormart}}
                  </template>
                </el-table-column>
                <el-table-column label="" width="100">
                  <template slot-scope="scope">
                    <el-tag :type="scope.row.examineStatus?'success':'danger'" >
                      {{scope.row.examineStatus?'':''}}
                    </el-tag>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </el-card>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click="dialogVisible = false"> </el-button>
          </span>
        </el-dialog>
      </el-main>
  </el-container>
</template>

<script>
export default {
  computed: {
    showList:function(){
      return this.applyList.filter((item)=>{
        if((item.applyState==1) &&(item.applyTitle.includes(this.search)||item.flow.flowName.includes(this.search))){
          return item
        }

      })
    }
  },
  data() {
    return {
      dialogVisible: false,
      applyList: [],
      search: '',
      applyInfo:null
    }
  },
  methods: {
     //
    downloadApplyFile(fileId,fileName){
      this.axios.get("/applyFile/download/"+fileId,{
        responseType:'blob'
      })
      .then((res)=>{
         console.log(res)
        if (window.navigator.msSaveBlob) {
          try {
          const blobObject = new Blob([res.data]);
          window.navigator.msSaveBlob(blobObject, fileName);
          } catch (e) {
            console.log(e);
          }
      } else {
          const blob = res.data;
          const reader = new FileReader();
          reader.readAsDataURL(blob);
          reader.onload = (e) => {
          const a = document.createElement('a');
          a.download = fileName;
          a.href = e.target.result;
          document.body.appendChild(a);
          a.click();
          document.body.removeChild(a);
          }
      }
      })
      .catch((res)=>{

      })
    },
    //
    cancelApply(index, row){
      this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          //
          this.axios.put("/apply/"+row.applyId,{applyState:0})
          .then((res)=>{
            if(res.data.status == 0){
              this.getUserApplyList()
            }
            else{
              this.$message.error(res.data.msg)
            }
          })
          .catch((res)=>{

          })
        }).catch(() => {
              
        });
      
    },
    //
    detailsApply(index, row) {
      this.axios.get("/apply/"+row.applyId)
      .then((res)=>{
        console.log(res)
        if(res.data.status == 0){
          this.applyInfo = res.data.data.apply
          this.dialogVisible = true
        }
        else{
          this.$message.error(res.data.msg)
        }
      })
      .catch((res)=>{

      })
    },
    getUserApplyList(){
      var info = JSON.parse(sessionStorage.getItem('userInfo'))
      this.axios.get('/applyList/'+info.userId)
      .then((res)=>{
        console.log(res)
        if(res.data.status == 0){
          this.applyList = res.data.data.applyList;
        }
        else{
          this.$message.error(res.data.msg)
        }
      })
      .catch((res)=>{
        this.$message.warning("")
      })
    }
  },
  created() {
    this.getUserApplyList()
  },
}
</script>

<style scoped>
 .file-item{
    margin-left: 5px;
    margin-right: 5px
  }
   .text {
    font-size: 13px;
  }

  .item {
    margin-bottom: 18px;
  }
</style>